import { useCallback, useRef, useState } from 'react'
import styles from './Popup.module.css'
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';

export default function Popup(){

    const fileRef = useRef(null);
    const titleRef = useRef(null);
    const subTitleRef = useRef(null);
    const authorRef = useRef(null);
    const [open, setOpen] = useState(false);

    async function fomrDateMake (e) {
        const formData = new FormData();
        const text = JSON.stringify({
            title : titleRef.current.value,
            subTitle : subTitleRef.current.value,
            author : authorRef.current.value
        });

        formData.append('files', e.target.files[0]);
        formData.append('body', text);

        return formData
    };

    const uploadFile = useCallback(async (e)=>{
        if(!e.target.files){
            return;
        } else if(!titleRef.current.value){
            return;
        }

        setOpen(true);

        const formData = await fomrDateMake(e);
        
        await fetch(`https://inkyuoh.shop/upload`,{
            method:'post',
            body : formData
        }).then((res)=>{
            setOpen(false);
        })
    },[])

    const clickUpload = useCallback(()=>{
        if (!fileRef.current){
            return;
        }
        fileRef.current.click();
    },[])

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };
    
    return (
        <div className={styles.popWrap}>
            <div className={styles.popBox}>
                <article className={styles.article}>
                    <p className={styles.pTitle}>My Book</p>
                    <p className={styles.pSubTitle}>ÌååÏùº ÏóÖÎ°úÎìú</p>
                    <div className={styles.inputWrap}>
                        <label className={styles.label}>üìåÏ±Ö ÌÉÄÏù¥ÌãÄ
                            <input ref={titleRef} className={styles.input} type='text' placeholder='Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.'></input>
                        </label>
                        <label className={styles.label}>ÏÑúÎ∏å ÌÉÄÏù¥ÌãÄ(option)
                            <input ref={subTitleRef} className={styles.input} type='text' placeholder='Î∂ÄÏ†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.'></input>
                        </label>
                        <label className={styles.label}>Ï†ÄÏûêÎ™Ö(option)
                            <input ref={authorRef} className={styles.input} type='text' placeholder='Ï†ÄÏûêÎ™ÖÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.'></input>
                        </label>
                    </div>
                </article>
                <aside className={styles.aside}>
                    <input type='file' accept='.pdf' className={styles.file} ref={fileRef} onChange={uploadFile}/>
                    <p className={styles.p}>
                        <h3><b>üö´Ï£ºÏùòÏÇ¨Ìï≠</b>üö´</h3><br/>
                        1. <b style={{color: 'red'}}>PDF ÌååÏùº</b>Îßå ÏóÖÎ°úÎìúÍ∞Ä Í∞ÄÎä•Ìï©ÎãàÎã§.<br/>
                        2. <b style={{color: 'red'}}>Ï†úÎ™© ÏûëÏÑ±</b> ÌõÑ ÌååÏùº ÏÑ†ÌÉùÏùÑ ÎàåÎü¨Ï£ºÏÑ∏Ïöî.<br/>
                        ‚Üí ÏûëÏÑ±ÌïòÏßÄ ÏïäÏùÑ Ïãú ÌååÏùºÏù¥ ÏóÖÎ°úÎìú ÎêòÏßÄ ÏïäÏäµÎãàÎã§!
                    </p>
                    <button className={styles.btn} onClick={clickUpload}>ÌååÏùº ÏóÖÎ°úÎìú</button>
                    <Snackbar open={open} autoHideDuration={3000} onClose={handleClose}>
                        <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
                            ÌååÏùº ÏóÖÎ°úÎìú Ï§ëÏûÖÎãàÎã§.
                        </Alert>
                    </Snackbar>
                </aside>
            </div>
        </div>
    )
}